<!--
TIMER
-->
<template>
  <div class="timer" v-router-page:page-name = "'timer'">
    <div class="timer-top">
      <div>T</div>
      <div>I</div>
      <div>M</div>
      <div>E</div>
      <div>R</div>
      <div>1</div>
      <div>5</div>
      <div>7</div>
      <div>6</div>
      <div>/</div>
      <div>1</div>
      <div>2</div>
      <div>0</div>
      <div></div>
      <div>R</div>
      <div>E</div>
      <div>C</div>
      <div>O</div>
      <div>R</div>
      <div>D</div>
    </div>
    <div class="timer-bottom">
      <div>f</div>
      <div>=</div>
      <div>{{receingNum[receingNum.length - 6]}}</div>
      <div>{{receingNum[receingNum.length - 5]}}</div>
      <div>{{receingNum[receingNum.length - 4]}}</div>
      <div>{{receingNum[receingNum.length - 3]}}</div>
      <div>{{receingNum[receingNum.length - 2]}}</div>
      <div>.</div>
      <div>{{receingNum[receingNum.length - 1]}}</div>
      <div>(</div>
      <div>0</div>
      <div>0</div>
      <div>)</div>
      <div>#</div>
      <div>H</div>
      <div>#</div>
      <div>R</div>
      <div>#</div>
      <div>E</div>
      <div>A</div>
    </div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      receingNum: this.$frame.gmdss.receingNum
    }
  },
  [EventKeys.EVT_MAN] (evt) {
  },
  [EventKeys.EVT_IOC] (evt) {
  },
  [EventKeys.EVT_SCAN] (evt) {
  },
  [EventKeys.EVT_PH] (evt) {
  },
  [EventKeys.EVT_DIM] (evt) {
  },
  [EventKeys.EVT_PEED] (evt) {
  },
  [EventKeys.EVT_HALF] (evt) {
  },
  [EventKeys.EVT_PROG] (evt) {
  },
  [EventKeys.EVT_TIME] (evt) {
  },
  [EventKeys.EVT_MO] (evt) {
  },
  [EventKeys.EVT_CH] (evt) {
  },
  [EventKeys.EVT_ENT] (evt) {
  },
  [EventKeys.EVT_ONE] (evt) {
  },
  [EventKeys.EVT_TWO] (evt) {
  },
  [EventKeys.EVT_THREE] (evt) {
  },
  [EventKeys.EVT_FOUR] (evt) {
  },
  [EventKeys.EVT_FIVE] (evt) {
  },
  [EventKeys.EVT_SIX] (evt) {
  },
  [EventKeys.EVT_SEVEN] (evt) {
  },
  [EventKeys.EVT_EIGHT] (evt) {
  },
  [EventKeys.EVT_NINE] (evt) {
  },
  [EventKeys.EVT_ZERO] (evt) {
  },
  [EventKeys.EVT_CLR + 'hold'] (evt) {
  },
  [EventKeys.EVT_WELL] (evt) {
  },
  [EventKeys.EVT_SURCE] (evt) {
  }
}
</script>

<style>
.timer{
  width: 100%;
  height: 100%;
  background-color: aquamarine;
  font-weight: bold;
}
.timer-top{
  display: flex;
  padding-top: 3px;
  justify-content: space-around;
}
.timer-top > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  text-align: center;
  line-height: 20px;
}
.timer-bottom{
  margin-top: 3px;
  display: flex;
  justify-content: space-around;
}
.timer-bottom > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  line-height: 20px;
  text-align: center;
}
</style>



