<!--

-->
<template>
  <div>
    <div class="auto-top1" v-show="!autoType">
      <div>W</div>
      <div>a</div>
      <div>i</div>
      <div>t</div>
      <div>i</div>
      <div>n</div>
      <div>g</div>
      <div></div>
      <div>s</div>
      <div>t</div>
      <div>a</div>
      <div>r</div>
      <div>t</div>
      <div></div>
      <div>s</div>
      <div>i</div>
      <div>g</div>
      <div>a</div>
      <div>n</div>
      <div>l</div>
    </div>
    <div class="auto-top1" v-show="autoType">
      <div>A</div>
      <div>U</div>
      <div>T</div>
      <div>O</div>
      <div>.</div>
      <div></div>
      <div>5</div>
      <div>7</div>
      <div>6</div>
      <div>/</div>
      <div>{{scan[0]}}</div>
      <div>{{scan[1]}}</div>
      <div>{{scan[2]}}</div>
      <div>{{scan[3]}}</div>
      <div>{{phase[0]}}{{record[0]}}</div>
      <div>{{phase[1]}}{{record[1]}}</div>
      <div>{{phase[2]}}{{record[2]}}</div>
      <div>{{phase[3]}}{{record[3]}}</div>
      <div>{{phase[4]}}{{record[4]}}</div>
      <div>{{record[5]}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      autoType: false,
      scan: 'Scan',
      phase: '',
      record: ''
    }
  },
  methods: {
    autoFn () {
      setTimeout(() => {
        this.autoType = true
      }, 3000)
    },
    scanFn () {
      setTimeout(() => {
        this.scan = '120'
        this.phase = 'Phase'
      }, 8000)
    },
    recordFn () {
      setTimeout(() => {
        this.phase = ''
        this.record = 'RECORD'
      }, 13000)
    }
  },
  mounted () {
    this.autoFn()
    this.scanFn()
    this.recordFn()
  }
}
</script>

<style>
.auto-top1{
  display: flex;
  padding-top: 3px;
  justify-content: space-around;
}
.auto-top1 > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  text-align: center;
  line-height: 20px;
}
</style>



