<!--
PH
-->
<template>
  <div class="rec-top">
    <div>#</div>
    <div>R</div>
    <div>E</div>
    <div>C</div>
    <div>.</div>
    <div></div>
    <div>5</div>
    <div>7</div>
    <div>6</div>
    <div>/</div>
    <div>1</div>
    <div>2</div>
    <div>0</div>
    <div></div>
    <div><div v-show= "ph">{{phText[phText.length - 3]}}</div></div>
    <div><div v-show= "ph">{{phText[phText.length - 2]}}</div></div>
    <div><div v-show= "ph">{{phText[phText.length - 1]}}</div></div>
    <div><div v-show= "ph">{{phNum1}}</div></div>
    <div><div v-show= "ph">{{phNum2}}</div></div>
    <div></div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  name: 'rec',
  data () {
    return {
      phText: 'PH=',
      ph: false,
      phNum: 0
    }
  },
  computed: {
    phNum1 () {
      return parseInt(this.phNum / 10)
    },
    phNum2 () {
      return this.phNum % 10
    }
  },
  methods: {
    phNums (num) {
      if (this.ph === true) {
        this.phNum = this.phNum * 10 + parseInt(num)
        this.phNum = this.phNum % 100
      }
    }
  },
  [EventKeys.EVT_MAN] (evt) {
    console.log('PH', '222222222222')
  },
  [EventKeys.EVT_IOC] (evt) {
  },
  [EventKeys.EVT_SCAN] (evt) {
  },
  [EventKeys.EVT_PH] (evt) {
    this.ph = true
    // this.phText = 'PH='
  },
  [EventKeys.EVT_DIM] (evt) {
  },
  [EventKeys.EVT_PEED] (evt) {
  },
  [EventKeys.EVT_HALF] (evt) {
  },
  [EventKeys.EVT_PROG] (evt) {
  },
  [EventKeys.EVT_TIME] (evt) {
  },
  [EventKeys.EVT_MO] (evt) {
  },
  [EventKeys.EVT_CH] (evt) {
  },
  [EventKeys.EVT_ENT] (evt) {
    this.ph = false
    this.phNum = 0
  },
  [EventKeys.EVT_ONE] (evt) {
    this.phNums('1')
  },
  [EventKeys.EVT_TWO] (evt) {
    this.phNums('2')
  },
  [EventKeys.EVT_THREE] (evt) {
    this.phNums('3')
  },
  [EventKeys.EVT_FOUR] (evt) {
    this.phNums('4')
  },
  [EventKeys.EVT_FIVE] (evt) {
    this.phNums('5')
  },
  [EventKeys.EVT_SIX] (evt) {
    this.phNums('6')
  },
  [EventKeys.EVT_SEVEN] (evt) {
    this.phNums('7')
  },
  [EventKeys.EVT_EIGHT] (evt) {
    this.phNums('8')
  },
  [EventKeys.EVT_NINE] (evt) {
    this.phNums('9')
  },
  [EventKeys.EVT_ZERO] (evt) {
    this.phNums('0')
  },
  [EventKeys.EVT_CLR] (evt) {
    this.$frame.popStack()
    this.$frame.click(evt)
  },
  [EventKeys.EVT_WELL] (evt) {
  }
}
</script>

<style>
.rec-top{
  display: flex;
  padding-top: 3px;
  justify-content: space-around;
}
.rec-top > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  text-align: center;
  line-height: 20px;
}
</style>



