<template>
  <div id="app" v-router-page:page-name = "'/'">
    <div class="app-left">
      <div class="app-ship" :style="brightness"><router-view/></div>
      <div class="left-btn1">
        <button id="man" @mousedown="mousedown('man')" @mouseup="mouseup('man')"></button>
        <button id="ioc" style="margin-left:60px;" @mousedown="mousedown('ioc')" @mouseup="mouseup('ioc')"></button>
        <button id="scan" @mousedown="mousedown('scan')" @mouseup="mouseup('scan')"></button>
        <button id="ph" @mousedown="mousedown('ph')" @mouseup="mouseup('ph')"></button>
      </div>
      <div class="left-btn2">
        <button id="dim" @mousedown="mousedown('dim')" @mouseup="mouseup('dim')"></button>
        <button id="peed" @mousedown="mousedown('peed')" @mouseup="mouseup('peed')"></button>
        <button id="half" @mousedown="mousedown('half')" @mouseup="mouseup('half')"></button>
        <button id="prog" @mousedown="mousedown('prog')" @mouseup="mouseup('prog')"></button>
        <button id="time" @mousedown="mousedown('time')" @mouseup="mouseup('time')"></button>
      </div>
    </div>
    <div class="app-right1">
      <div class='right1-div'>
      <div :style="brightness" :class= "surceNum === -1 ? 'bg-color' : 'undefined'"></div>
      <div></div>
      <div :style="brightness" :class= "surceNum === 1 ? 'bg-color' : 'undefined'"></div>
      </div>
      <div class="right1-div2">
        <div style="margin-top:0px;"><button id="mo" @mousedown="mousedown('mo')" @mouseup="mouseup('mo')"></button></div>
        <div><button id="ch" @mousedown="mousedown('ch')" @mouseup="mouseup('ch')"></button></div>
        <div><button id="ent" @mousedown="mousedown('ent')" @mouseup="mouseup('ent')"></button></div>
      </div>
    </div>
    <div class="app-right2">
      <div style="margin-top:0px;">
        <button id="one" @mousedown="mousedown('one')" @mouseup="mouseup('one')"></button>
        <button id="two" @mousedown="mousedown('two')" @mouseup="mouseup('two')"></button>
        <button id="three" @mousedown="mousedown('three')" @mouseup="mouseup('three')"></button>
      </div>
      <div>
        <button id="four" @mousedown="mousedown('four')" @mouseup="mouseup('four')"></button>
        <button id="five" @mousedown="mousedown('five')" @mouseup="mouseup('five')"></button>
        <button id="six" @mousedown="mousedown('six')" @mouseup="mouseup('six')"></button>
      </div>
      <div>
        <button id="seven" @mousedown="mousedown('seven')" @mouseup="mouseup('seven')"></button>
        <button id="eight" @mousedown="mousedown('eight')" @mouseup="mouseup('eight')"></button>
        <button id="nine" @mousedown="mousedown('nine')" @mouseup="mouseup('nine')"></button>
      </div>
      <div>
        <button id="clr" @mousedown="mousedown('clr')" @mouseup="mouseup('clr')"></button>
        <button id="zero" @mousedown="mousedown('zero')" @mouseup="mouseup('zero')"></button>
        <button id="well" @mousedown="mousedown('well')" @mouseup="mouseup('well')"></button>
      </div>
    </div>
    <div class="rotate1"><dial></dial></div>
    <div class="rotate2"><dial></dial></div>
    <div v-show="!paper" class="shadow"><div id="mypaper" @click="paperFn"  class="bg-top"  @mousewheel="doMouseWheel()"></div></div>
    <div class="bg-paper">
      <div class="bg-paper1">
        <div @click="paperFn" v-show="paper" class="bg-paper2" :style="porderTop"></div>
      </div>
    </div>
    <div class="app-broadside">
      <button :class="{'surce-off':surceNum == 0, 'surce-on':surceNum == 1, 'surce-timer':surceNum == -1}" @mousedown="mousedown('surce')" @mouseup="mouseup('surce')"></button>
    </div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
import dial from './components/dial.vue'
export default {
  name: 'App',
  data () {
    return {
      issurce: false,
      surceNum: 0,
      gmdss: this.$frame.gmdss,
      // paperNum: -400,
      intervalId: '', // 
      paper: true,
      brightnessNum: 0, // 
      brightnessType: false, // 
      widthbit: 8
    }
  },
  watch: {
    // 
    surceNum (ovel, nevl) {
      if (ovel === 1) {
        this.issurce = true
        this.$frame.routeTo('registering')
        this.gmdss.power = true
      }
      if (ovel === -1) {
        this.issurce = false
        this.gmdss.power = true
        if (this.gmdss.timerType === true) {
          this.$frame.routeTo('timer')
          this.gmdss.timerType = false
        }
      }
      if (ovel === 0) {
        this.gmdss.power = false
        this.$frame.routeTo('/')
        this.gmdss.lightness = 10
        this.gmdss.print = false
        this.gmdss.paperNum = -400
        this.paper = true
      }
    },
    print (ovel, nevl) {
      if (ovel === true) {
        this.porderFn()
      }
    },
    // 
    brightnessNum (ovel, nevl) {
      if (ovel === -1) {
        this.brightnessType = true
        this.gmdss.lightness = 0
      }
      if (ovel === 1) {
        this.brightnessType = false
        this.gmdss.lightness = 20
      }
      if (ovel === 0) {
        this.gmdss.lightness = 10
      }
    },
    // 
    paperNum (ovel, nevl) {
      if (ovel === 0) {
        this.gmdss.dbprint = true
        this.gmdss.print = false
      }
    }
  },
  computed: {
    porderTop () {
      let num = this.paperNum
      let s = {top: `${num}px`, '-webkit-top': `${num}px`}
      return s
    },
    print () {
      return this.$frame.gmdss.print
    },
    // 
    brightness () {
      let num = parseInt(this.gmdss.lightness / 2) + 6
      let value = num / 10
      let s = `{filter: brightness(${value}); -webkit-filter: brightness(${value});}`
      return s
    },
    // top
    paperNum () {
      return this.$frame.gmdss.paperNum
    }
  },
  methods: {
    mousedown (keyname) {
      if (this.holdonTimeout >= 0) {
        clearTimeout(this.holdonTimeout)
        this.holdonTimeout = -1
      }

      let _this = this
      // 500
      this.holdonTimeout = setTimeout(function () {
        _this.btnhold(keyname)
        _this.holdonTimeout = -1
      }, 3000)
    },
    // 
    mouseup (keyname) {
      // mouseup
      if (this.holdonTimeout >= 0) {
        clearTimeout(this.holdonTimeout)
        this.btnclick(keyname)
      }
      this.holdonTimeout = -1
    },
    // sss
    btnhold: function (keyname) {
      let fnname = EventKeys.EVT_MAP[keyname]
      let name = fnname + 'hold'
      let evt = {
        app: this,
        anykey: true,
        continue: true,
        currentpath: this.$frame.getCurrentPath(),
        holdon: true,
        keyname: name
      }
      this.btnPreprocess(evt)
      this.$frame.click(evt)
    },
    // 
    btnclick: function (keyname) {
      let fnname = EventKeys.EVT_MAP[keyname]
      let evt = {
        app: this,
        anykey: true,
        continue: true,
        currentpath: this.$frame.getCurrentPath(),
        holdon: true,
        keyname: fnname
      }

      this.btnPreprocess(evt)
      this.$frame.click(evt)
    },
    btnPreprocess (evt) {
      // btn  
      // 1 ,  App.vue   page
      // 2  any 
      let keyname = evt.keyname
      let fn = this.$options[keyname]
      let currentpath = this.$frame.getCurrentPath()
      if (fn === undefined) {
        console.warn('App.Vue [' + fn + ']()')
      } else if (currentpath !== '/') {
        fn.call(this)
      }

      let tmpkey = evt.keyname
      evt.keyname = 'any'
      this.$frame.click(evt)
      evt.keyname = tmpkey
    },
    // 
    porderFn () {
      let _this = this
      _this.intervalId = setInterval(function () {
        if (_this.$frame.gmdss.paperNum < 0) {
          _this.$frame.gmdss.paperNum += 10
        }
        if (_this.$frame.gmdss.paperNum >= 0) {
          clearInterval(_this.intervalId)
        }
      }, 500)
    },
    // 
    paperFn () {
      if (this.paperNum >= 0) {
        this.paper = !this.paper
      }
    },
    //  
    doMouseWheel (event) {
      event = event || window.event
      // event.wheelDelta 120-120
      // event.originalEvent.wheelDelta()
      if (event.wheelDelta > 0) {
        if (this.widthbit < 10) {
          this.widthbit++
        }
      } else {
        if (this.widthbit > 2) {
          this.widthbit--
        }
      }
      // , 
      let w = 74.5 * this.widthbit
      let h = 64 * this.widthbit
      let top = (700 - h) / 2
      let left = (950 - w) / 2
      document.getElementById('mypaper').style.top = top + 'px'
      document.getElementById('mypaper').style.left = left + 'px'
      document.getElementById('mypaper').style.width = w + 'px'
      document.getElementById('mypaper').style.height = h + 'px'
      document.getElementById('mypaper').style.backgroundSize = w + 'px' + ' ' + h + 'px'
      event.returnValue = false // 
    }
  },
  components: {
    dial
  },
  [EventKeys.EVT_MAN] (evt) {
  },
  [EventKeys.EVT_IOC] (evt) {
  },
  [EventKeys.EVT_SCAN] (evt) {
  },
  [EventKeys.EVT_PH] (evt) {
  },
  [EventKeys.EVT_DIM] (evt) {
    if (this.gmdss.power === true) {
      if (this.brightnessType === false) {
        this.brightnessNum -= 1
      } else {
        this.brightnessNum += 1
      }
    }
  },
  [EventKeys.EVT_PEED] (evt) {
  },
  [EventKeys.EVT_HALF] (evt) {
  },
  [EventKeys.EVT_PROG] (evt) {
  },
  [EventKeys.EVT_TIME] (evt) {
  },
  [EventKeys.EVT_MO] (evt) {
  },
  [EventKeys.EVT_CH] (evt) {
  },
  [EventKeys.EVT_ENT] (evt) {
  },
  [EventKeys.EVT_ONE] (evt) {
  },
  [EventKeys.EVT_TWO] (evt) {
  },
  [EventKeys.EVT_THREE] (evt) {
  },
  [EventKeys.EVT_FOUR] (evt) {
  },
  [EventKeys.EVT_FIVE] (evt) {
  },
  [EventKeys.EVT_SIX] (evt) {
  },
  [EventKeys.EVT_SEVEN] (evt) {
  },
  [EventKeys.EVT_EIGHT] (evt) {
  },
  [EventKeys.EVT_NINE] (evt) {
  },
  [EventKeys.EVT_ZERO] (evt) {
  },
  [EventKeys.EVT_CLR + 'hold'] (evt) {
  },
  [EventKeys.EVT_WELL] (evt) {
  },
  [EventKeys.EVT_SURCE] (evt) {
    if (this.issurce === false) {
      this.surceNum += 1
      console.log(this.surceNum, '55555555555555')
    } else {
      this.surceNum -= 1
      console.log(this.surceNum, '88888888888888')
    }
  }
}
</script>

