<!--

-->
<template>
  <div v-router-page:page-name = "'registering'" class="registering">
    <waiting v-show= "index === 0"></waiting>
    <manu ref="manus" v-show= "index === 1"></manu>
    <rec ref="rec" v-show= "index === 3"></rec>
    <times ref="times" v-show= "index === 4"></times>
    <preserd v-show= "index === 5"></preserd>
    <div class="registering-bottom">
      <div>f</div>
      <div>=</div>
      <div>{{receing[receing.length - 6]}}</div>
      <div>{{receing[receing.length - 5]}}</div>
      <div>{{receing[receing.length - 4]}}</div>
      <div>{{receing[receing.length - 3]}}</div>
      <div>{{receing[receing.length - 2]}}</div>
      <div>.</div>
      <div>{{receing[receing.length - 1]}}</div>
      <div>(</div>
      <div>{{isCh1}}</div>
      <div>{{isCh2}}</div>
      <div>)</div>
      <div>#</div>
      <div>{{hale}}</div>
      <div>#</div>
      <div>{{rev}}</div>
      <div>#</div>
      <div>{{ext}}</div>
      <div>{{att}}</div>
    </div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
import manu from '../components/manu.vue'
import waiting from '../components/waiting.vue'
import rec from '../components/rec.vue'
import times from '../components/time.vue'
import preserd from '../components/preserd.vue'
export default {
  name: 'registering',
  data () {
    return {
      receing: '',
      ch: 0,
      frequencCh: false, // 
      moReq: false, // 
      index: 0,
      hale: null, // HALE 
      well: false, // 
      ext: '', //  # + 2  E
      rev: '', // # + 1 R
      att: '', // # + 3 A
      pressed: false, // 
      presseds: false // 
    }
  },
  computed: {
    isCh1 () {
      return parseInt(this.ch / 10)
    },
    isCh2 () {
      return this.ch % 10
    },
    myCh () {
      return this.ch
    }
  },
  watch: {
    // 
    receing (ovel, nevl) {
      this.$frame.gmdss.receingNum = ovel
    },
    index (ovel, nevl) {
      if (ovel === 1) {
        this.$frame.pushStack('manu', this.$refs.manus)
      }
      if (this.index === 3) {
        this.$frame.pushStack('rec', this.$refs.rec)
      }
      if (ovel === 4) {
        this.$frame.pushStack('times', this.$refs.times)
      }
    }
  },
  methods: {
    setFreq: function (num) {
      if (this.receing.length < 6 && this.frequencCh === false && this.moReq === false) {
        this.receing += num
      }
      if (this.frequencCh === true && this.moReq === false) {
        this.ch = this.ch * 10 + parseInt(num)
        this.ch = this.ch % 100
      }
      if (this.moReq === true) {
        let _this = this
        this.ch = this.ch * 10 + parseInt(num)
        this.ch = this.ch % 100
        let dataList = this.$frame.gmdss.freqList.filter(function (item) {
          return item.num === _this.myCh
        })
        if (dataList[0].frequenc === undefined) {
          return 0
        } else {
          this.receing = dataList[0].frequenc
        }
      }
    }
  },
  components: {
    manu,
    waiting,
    rec,
    times,
    preserd
  },
  [EventKeys.EVT_MAN] (evt) {
    if (this.well === false) {
      if (this.index === 0) {
        this.index = 1
      } else {
        this.index = 0
      }
    } else {
      this.index = 3
      this.well = false
    }
  },
  [EventKeys.EVT_IOC] (evt) {
  },
  [EventKeys.EVT_SCAN] (evt) {
  },
  [EventKeys.EVT_PH] (evt) {
  },
  [EventKeys.EVT_DIM] (evt) {
  },
  [EventKeys.EVT_PEED] (evt) {
  },
  [EventKeys.EVT_HALF] (evt) {
    this.hale = 'H'
  },
  [EventKeys.EVT_PROG] (evt) {
    this.$frame.routeTo('prog')
  },
  [EventKeys.EVT_TIME] (evt) {
    this.index = 4
  },
  [EventKeys.EVT_MO] (evt) {
    this.moReq = true
    this.receing = ''
  },
  [EventKeys.EVT_CH] (evt) {
    this.frequencCh = true
  },
  [EventKeys.EVT_ENT] (evt) {
    let object = {
      // 
      frequenc: this.receing,
      // 
      num: this.ch
    }
    if (this.ch < 99) {
      this.ch += 1
    }
    if (this.receing.length > 0) {
      this.$frame.gmdss.freqList.push(object)
    }
    // 
    this.receing = ''
    // 
    this.frequencCh = false
    // HALE
    this.hale = null
  },
  [EventKeys.EVT_ONE] (evt) {
    // 
    if (this.well === true) {
      this.rev = 'R'
      this.well = false
    } else {
      this.setFreq('1')
    }
  },
  [EventKeys.EVT_TWO] (evt) {
    if (this.well === true) {
      this.ext = 'E'
      this.well = false
    } else {
      this.setFreq('2')
    }
  },
  [EventKeys.EVT_THREE] (evt) {
    if (this.well === true) {
      this.att = 'A'
      this.well = false
    } else {
      this.setFreq('3')
    }
  },
  [EventKeys.EVT_FOUR] (evt) {
    this.setFreq('4')
  },
  [EventKeys.EVT_FIVE] (evt) {
    this.setFreq('5')
  },
  [EventKeys.EVT_SIX] (evt) {
    if (this.pressed === false) {
      this.setFreq('6')
    } else {
      let parse = parseInt(this.receing)
      parse += 1
      this.receing = parse + ''
      // console.log(parse)
    }
  },
  [EventKeys.EVT_SEVEN] (evt) {
    if (this.well === false) {
      this.setFreq('7')
    } else {
      this.presseds = true
    }
  },
  [EventKeys.EVT_EIGHT] (evt) {
    if (this.well === false) {
      this.setFreq('8')
    } else {
      if (this.presseds === true) {
        this.index = 5
        this.pressed = true
        this.well = false
      }
    }
  },
  [EventKeys.EVT_NINE] (evt) {
    if (this.pressed === false) {
      this.setFreq('9')
    } else {
      let parse = parseInt(this.receing)
      parse -= 1
      this.receing = parse + ''
      // console.log(parse)
    }
  },
  [EventKeys.EVT_ZERO] (evt) {
    this.setFreq('0')
  },
  [EventKeys.EVT_CLR] (evt) {
    if (this.pressed === false) {
      this.receing = ''
      this.ch = 0
      this.frequencCh = false
      this.moReq = false
      this.index = 0
      this.hale = null
    } else {
      this.pressed = false
      this.index = 0
      this.presseds = false
    }
  },
  [EventKeys.EVT_WELL] (evt) {
    this.well = true
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.index = 0
    })
  }
}
</script>

<style>
.registering{
  width: 100%;
  height: 100%;
  background-color: aquamarine;
  font-weight: bold;
}
.registering-top{
  display: flex;
  padding-top: 3px;
  justify-content: space-around;
}
.registering-top > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  text-align: center;
  line-height: 20px;
}
.registering-bottom{
  margin-top: 3px;
  display: flex;
  justify-content: space-around;
}
.registering-bottom > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  line-height: 20px;
  text-align: center;
}
</style>



