<!--
manu 
-->
<template>
  <div class="manu-top">
      <div>M</div>
      <div>A</div>
      <div>N</div>
      <div>U</div>
      <div>.</div>
      <div></div>
      <div>{{this.ioc[this.ioc.length - 3]}}</div>
      <div>{{this.ioc[this.ioc.length - 2]}}</div>
      <div>{{this.ioc[this.ioc.length - 1]}}</div>
      <div>/</div>
      <div>{{this.scan[this.scan.length - 3]}}</div>
      <div>{{this.scan[this.scan.length - 2]}}</div>
      <div>{{this.scan[this.scan.length - 1]}}</div>
      <div></div>
      <div>P</div>
      <div>h</div>
      <div>a</div>
      <div>s</div>
      <div>e</div>
      <div></div>
    </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  name: 'manu',
  data () {
    return {
      ioc: '288',
      scan: '120',
      // iocList: ['288', '576'], //  288  576
      iocindex: 0, // 
      // scanList: ['60', '90', '120', '240'], // 
      scanindex: 0, // 
      gmdss: this.$frame.gmdss
    }
  },
  watch: {
    iocindex (ovel, nevl) {
      if (this.iocindex > 0) {
        this.iocindex = -1
      }
    },
    scanindex (ovel, nevl) {
      if (this.scanindex > 2) {
        this.scanindex = -1
      }
    }
  },
  [EventKeys.EVT_MAN] (evt) {
    this.$frame.popStack()
    this.$frame.click(evt)
  },
  [EventKeys.EVT_IOC] (evt) {
    this.iocindex += 1
    this.ioc = this.gmdss.iocList[this.iocindex]
  },
  [EventKeys.EVT_SCAN] (evt) {
    this.scanindex += 1
    this.scan = this.gmdss.scanList[this.scanindex]
  },
  [EventKeys.EVT_CLR] (evt) {
    this.$frame.popStack()
    // this.$frame.routeTo('registering')
  }
}
</script>

<style>
.manu-top{
  display: flex;
  padding-top: 3px;
  justify-content: space-around;
}
.manu-top > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  text-align: center;
  line-height: 20px;
}
</style>



