<template>
  <div class="hello">
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  [EventKeys.EVT_MAN] (evt) {
  },
  [EventKeys.EVT_IOC] (evt) {
  },
  [EventKeys.EVT_SCAN] (evt) {
  },
  [EventKeys.EVT_PH] (evt) {
  },
  [EventKeys.EVT_DIM] (evt) {
  },
  [EventKeys.EVT_PEED] (evt) {
  },
  [EventKeys.EVT_HALF] (evt) {
  },
  [EventKeys.EVT_PROG] (evt) {
  },
  [EventKeys.EVT_TIME] (evt) {
  },
  [EventKeys.EVT_MO] (evt) {
  },
  [EventKeys.EVT_CH] (evt) {
  },
  [EventKeys.EVT_ENT] (evt) {
  },
  [EventKeys.EVT_ONE] (evt) {
  },
  [EventKeys.EVT_TWO] (evt) {
  },
  [EventKeys.EVT_THREE] (evt) {
  },
  [EventKeys.EVT_FOUR] (evt) {
  },
  [EventKeys.EVT_FIVE] (evt) {
  },
  [EventKeys.EVT_SIX] (evt) {
  },
  [EventKeys.EVT_SEVEN] (evt) {
  },
  [EventKeys.EVT_EIGHT] (evt) {
  },
  [EventKeys.EVT_NINE] (evt) {
  },
  [EventKeys.EVT_ZERO] (evt) {
  },
  [EventKeys.EVT_CLR + 'hold'] (evt) {
  },
  [EventKeys.EVT_WELL] (evt) {
  },
  [EventKeys.EVT_SURCE] (evt) {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



