import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import registering from '../pages/registering.vue'
import prog from '../pages/prog.vue'
import timer from '../pages/timer.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/registering',
      name: 'registering',
      component: registering
    },
    {
      path: '/prog',
      name: 'prog',
      component: prog
    },
    {
      path: '/timer',
      name: 'timer',
      component: timer
    }
  ]
})



