<!--

-->
<template>
  <div class="time-top">
    <div><div class="time-div"></div></div>
    <div :class= "index === 0 ? 'time-border' : 'undefined'">{{housText[0]}}</div>
    <div :class= "index === 1 ? 'time-border' : 'undefined'">{{housText[housText.length - 1]}}</div>
    <div>:</div>
    <div :class= "index === 2 ? 'time-border' : 'undefined'">{{minutess[0]}}</div>
    <div :class= "index === 3 ? 'time-border' : 'undefined'">{{minutess[minutess.length - 1]}}</div>
    <div>:</div>
    <div :class= "index === 4 ? 'time-border' : 'undefined'">{{secondes[0]}}</div>
    <div :class= "index === 5 ? 'time-border' : 'undefined'">{{secondes[secondes.length - 1]}}</div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
  </div>
</template>

<script>
import numeral from 'numeral'
import * as EventKeys from '@/utils/event-key'
export default {
  name: 'times',
  data () {
    return {
      housText: '',
      minutess: '',
      secondes: '',
      index: 0
    }
  },
  computed: {
  },
  mounted () {
    this.times()
  },
  methods: {
    times () {
      this.housText = this.$frame.gmdss.time.getHours() + ''
      this.minutess = this.$frame.gmdss.time.getMinutes() + ''
      this.secondes = this.$frame.gmdss.time.getSeconds() + ''
      this.secondes = numeral(this.secondes).format('00')
      this.housText = numeral(this.housText).format('00')
      this.minutess = numeral(this.minutess).format('00')
    },
    indexNum () {
      if (this.index < 5) {
        this.index += 1
      }
    },
    numText (num) {
      switch (this.index) {
        case 0:
          this.housText = num + this.housText
          this.indexNum()
          break
        case 1:
          this.housText = this.housText + num
          this.indexNum()
          break
        case 2:
          this.minutess = num + this.minutess
          this.indexNum()
          break
        case 3:
          this.minutess = this.minutess + num
          this.indexNum()
          break
        case 4:
          this.secondes = num + this.secondes
          this.indexNum()
          break
        case 5:
          this.secondes = this.secondes + num
          this.indexNum()
          break
      }
    }
  },
  [EventKeys.EVT_MAN] (evt) {
  },
  [EventKeys.EVT_IOC] (evt) {
  },
  [EventKeys.EVT_SCAN] (evt) {
  },
  [EventKeys.EVT_PH] (evt) {
  },
  [EventKeys.EVT_DIM] (evt) {
  },
  [EventKeys.EVT_PEED] (evt) {
  },
  [EventKeys.EVT_HALF] (evt) {
  },
  [EventKeys.EVT_PROG] (evt) {
  },
  [EventKeys.EVT_TIME] (evt) {
    // 
    this.times()
    this.index = 0
    // this.$frame.gmdss.timerType = true
    if (this.$frame.gmdss.itemType === true) {
      this.$frame.gmdss.itemType = false
    }
  },
  [EventKeys.EVT_MO] (evt) {
  },
  [EventKeys.EVT_CH] (evt) {
  },
  [EventKeys.EVT_ENT] (evt) {
    let item = this.$frame.gmdss.time
    // 
    let yellar = item.getFullYear()
    console.log(yellar)
    // 
    let month = item.getMonth() + 1
    // 
    let dates = item.getDate()
    // 
    let houst = this.housText[0] + this.housText[this.housText.length - 1]
    // 
    let minute = this.minutess[0] + this.minutess[this.minutess.length - 1]
    // 
    let secode = this.secondes[0] + this.secondes[this.secondes.length - 1]
    // 
    let str = yellar + '-' + month + '-' + dates + ' ' + houst + ':' + minute + ':' + secode
    this.$frame.gmdss.times = new Date(str)
    // 
    console.log(yellar + '-' + month + '-' + dates + ' ' + houst + ':' + minute + ':' + secode)
    // 
    this.$frame.gmdss.itemType = true
    // 
    if (this.$frame.gmdss.print === false) {
      this.$frame.gmdss.paperNum = -400
    }
  },
  [EventKeys.EVT_ONE] (evt) {
    this.numText('1')
  },
  [EventKeys.EVT_TWO] (evt) {
    this.numText('2')
  },
  [EventKeys.EVT_THREE] (evt) {
    this.numText('3')
  },
  [EventKeys.EVT_FOUR] (evt) {
    this.numText('4')
  },
  [EventKeys.EVT_FIVE] (evt) {
    this.numText('5')
  },
  [EventKeys.EVT_SIX] (evt) {
    this.numText('6')
  },
  [EventKeys.EVT_SEVEN] (evt) {
    this.numText('7')
  },
  [EventKeys.EVT_EIGHT] (evt) {
    this.numText('8')
  },
  [EventKeys.EVT_NINE] (evt) {
    this.numText('9')
  },
  [EventKeys.EVT_ZERO] (evt) {
    this.numText('0')
  },
  [EventKeys.EVT_CLR] (evt) {
    this.$frame.popStack()
    this.$frame.click(evt)
  },
  [EventKeys.EVT_WELL] (evt) {
  }
}
</script>

<style>
.time-top{
  display: flex;
  padding-top: 3px;
  justify-content: space-around;
}
.time-top > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  text-align: center;
  line-height: 20px;
}
.time-border{
  border-bottom: 1px solid black;
}
.time-div{
  width:5px;
  height:7px;
  background-color:black;
  margin-left:2px;
  margin-top:5px;
}
</style>



