import Vue from 'vue'

import {VueFrame} from './frame'

let frame = new VueFrame()

window.$frame = frame

function fninstall (Vue, options) {
  //  
  Vue.directive('router-page', {
    bind: function (el, binding, vnode) {
      let vm = vnode.context

      //  page  vm 
      let pagename = binding.value

      if (vm === undefined) {
        console.warn('page:' + pagename + '')
      } else {
        let options = vm.$options

        if (!options['beforePageRouteEnter']) {
          console.warn(' ' + pagename + ', beforePageRouteEnter()')
        }

        if (!options['beforePageRouteUpdate']) {
          console.warn(' ' + pagename + ', beforePageRouteUpdate()')
        }

        if (!options['beforePageRouteLeave']) {
          console.warn(' ' + pagename + ', beforePageRouteLeave()')
        }
      }
      frame.addVm(pagename, vm)
    },
    unbind: function (el, binding) {
      let pagename = binding.value
      frame.removeVm(pagename)
    }
  })

  if (VueFrame.installed) { return }
  VueFrame.installed = true

  let _Vue = Vue

  //   gmdss  vue 
  _Vue.mixin({
    beforeCreate: function beforeCreate () {
      this._frame = frame
      Vue.util.defineReactive(this, '$frame', frame)
    }
  })

  Object.defineProperty(_Vue, '$frame', {
    get: function get () { return this._frame }
  })
}

VueFrame.installed = false
VueFrame.install = fninstall

Vue.use(VueFrame)

export default frame



