export class VueGMDSS {
  constructor () {
    // 
    // 
    /*
    * {
      frequenc 
      num 
    }
    */
    this.freqList = []
    // 
    this.receingNum = ''
    // 
    this.print = false
    // 
    this.time = new Date()
    this.times = new Date()
    // 
    this.itemType = false
    // prog
    /*
    {
      chaNum 
      time1Text 1
      time2Text 2
      fchNum fch
      manu 
      half HALFS
    }
    */
    // 
    this.lightness = 10
    // 
    this.power = false
    // 
    this.progAutoList = []
    // 
    this.progManuList = []
    // ioc
    this.iocList = ['288', '576']
    // scan 
    this.scanList = ['60', '90', '120', '240']
    // 
    this.timerType = false
    // 
    this.dbprint = false
    // top
    this.paperNum = -400
    // 
    this.userinfo = {
      'mmsi': '',
      'imo': '',
      'callsign': '',
      'name': '',
      'phone': '',
      'email': '',
      'arqid5': '',
      'arqid9': '',
      'gfecid5': '',
      'gfecid9': '',
      'lat': 18.12,
      'lon': -20.20,
      'allowrun': '',
      'item': ''
    }
  }
  doTimer () {
    let time = this.time.getTime()
    time += 1000
    this.time = new Date(time)
    if (this.itemType === true) {
      if (this.times <= this.time) {
        this.print = true
        console.log('')
        this.itemType = false
      }
      console.log(this.time)
    }
  }
}



