<!--
PROG
-->
<template>
  <div class="prog" v-router-page:page-name = "'prog'"  >
    <div class="prog-top">
      <div style="text-align:center"><div class="prog-div"></div></div>
      <div>C</div>
      <div>H</div>
      <div>=</div>
      <div :class= "index1 === 0 ? 'prog-border' : 'undefined'">{{channel}}</div>
      <div></div>
      <div><div class="prog-div"></div></div>
      <div :class= "index1 === 1 ? 'prog-border' : 'undefined'">{{timeText1[0]}}</div>
      <div :class= "index1 === 2 ? 'prog-border' : 'undefined'">{{timeText1[1]}}</div>
      <div>:</div>
      <div :class= "index1 === 3 ? 'prog-border' : 'undefined'">{{timeText1[2]}}</div>
      <div :class= "index1 === 4 ? 'prog-border' : 'undefined'">{{timeText1[3]}}</div>
      <div class="prog-right"></div>
      <div :class= "index1 === 5 ? 'prog-border' : 'undefined'">{{timeText2[0]}}</div>
      <div :class= "index1 === 6 ? 'prog-border' : 'undefined'">{{timeText2[1]}}</div>
      <div>:</div>
      <div :class= "index1 === 7 ? 'prog-border' : 'undefined'">{{timeText2[2]}}</div>
      <div :class= "index1 === 8 ? 'prog-border' : 'undefined'">{{timeText2[3]}}</div>
      <div></div>
      <div></div>
    </div>
    <div class="prog-bottom">
      <div><div class="prog-div"></div></div>
      <div>f</div>
      <div>C</div>
      <div>H</div>
      <div>=</div>
      <div>{{isgallery1}}</div>
      <div :class= "index2 === 1 ? 'prog-border' : 'undefined'">{{isgallery2}}</div>
      <div></div>
      <div><div class="prog-div"></div></div>
      <div v-show="!iocShow" :class= "index2 === 2 ? 'prog-border' : 'undefined'"><div>{{autoText[0]}}</div></div>
      <div v-show="!iocShow"><div>{{autoText[1]}}</div></div>
      <div v-show="!iocShow"><div>{{autoText[2]}}</div></div>
      <div v-show="!iocShow"><div>{{autoText[3]}}</div></div>
      <div v-show="iocShow" :class= "index2 === 2 ? 'prog-border' : 'undefined'">{{this.ioc[this.ioc.length - 3]}}</div>
      <div v-show="iocShow">{{this.ioc[this.ioc.length - 2]}}</div>
      <div v-show="iocShow">{{this.ioc[this.ioc.length - 1]}}</div>
      <div v-show="iocShow">/</div>
      <div :class= "index2 === 4 ? 'prog-border' : 'undefined'"><div v-show="scanShow">{{this.scan[this.scan.length - 3]}}</div></div>
      <div><div v-show="scanShow">{{this.scan[this.scan.length - 2]}}</div></div>
      <div><div v-show="scanShow">{{this.scan[this.scan.length - 1]}}</div></div>
      <div></div>
      <div><div class="prog-div"></div></div>
      <div :class= "index2 === 3 ? 'prog-border' : 'undefined'">{{halfs}}</div>
      <div></div>
    </div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  name: 'prog',
  data () {
    return {
      gmdss: this.$frame.gmdss,
      index1: 0, // 
      channel: '', // 
      timeText1: '', // 1
      timeText2: '', // 2
      index2: 0, // 
      galleryType: false, // 
      gallery: 0, // 
      autoText: '', // 
      halfs: '', // HALFS 
      aotoNum: false, //  AUTOMANU
      ioc: '', // 
      scan: '', // 
      iocShow: false, // ioc
      scanShow: false, // scan
      iocindex: 0, // 
      scanindex: 0, // 
      progList: false, //  true false
      clear: 0 // cler >= 4 
    }
  },
  computed: {
    // 
    isgallery1 () {
      return parseInt(this.gallery / 10)
    },
    isgallery2 () {
      return this.gallery % 10
    }
  },
  watch: {
    iocindex () {
      if (this.iocindex > 0) {
        this.iocindex = -1
      }
    },
    scanindex (ovel, nevl) {
      if (this.scanindex > 2) {
        this.scanindex = -1
      }
    }
  },
  methods: {
    // 
    channelNum (num) {
      switch (this.index1) {
        // 
        case 0:
          this.channel = num
          break
        //  
        case 1:
          if (parseInt(num) <= 2) {
            this.timeText1 = this.timeText1 + num
            this.index1 += 1
          }
          break
        //  
        case 2:
          if (parseInt(num) <= 3) {
            this.timeText1 = this.timeText1 + num
            this.index1 += 1
          }
          break
        //  
        case 3:
          if (parseInt(num) <= 5) {
            this.timeText1 = this.timeText1 + num
            this.index1 += 1
          }
          break
        //  
        case 4:
          if (parseInt(num) <= 9) {
            this.timeText1 = this.timeText1 + num
          }
          break
        //  
        case 5:
          if (parseInt(num) >= this.timeText1[0] && parseInt(num) <= 2) {
            this.timeText2 = this.timeText2 + num
            this.index1 += 1
          }
          break
        //  
        case 6:
          if (parseInt(num) >= this.timeText1[1] && parseInt(num) <= 3) {
            this.timeText2 = this.timeText2 + num
            this.index1 += 1
          }
          break
        //  
        case 7:
          if (parseInt(num) >= this.timeText1[2] && parseInt(num) <= 5) {
            this.timeText2 = this.timeText2 + num
            this.index1 += 1
          }
          break
        //  
        case 8:
          if (parseInt(num) >= this.timeText1[3] && parseInt(num) <= 9) {
            this.timeText2 = this.timeText2 + num
          }
      }
    },
    // 
    index1Fun () {
      if (this.progList === false) {
        if (this.channel.length === 1 && this.timeText1 === '') {
          this.index1 = 1
        }
        if (this.timeText1.length >= 4 && this.timeText2 === '') {
          this.index1 = 5
        }
        if (this.timeText2.length >= 4 && this.galleryType === false) {
          this.index2 = 1
          this.galleryType = true
        }
      } else {
        if (this.channel.length === 1) {
          this.index1 += 3
        }
        if (this.index1 > 8 && this.galleryType === false) {
          this.index2 = 1
          this.galleryType = true
        }
      }
    },
    // 
    index2Fun () {
      if (this.progList === false) {
        if (this.timeText2.length >= 4) {
          this.index2 = 1
        }
        if (this.gallery >= 0) {
          this.index2 = 2
        }
        if (this.autoText === 'AUTO') {
          this.index2 = 3
        }
        if (this.autoText === 'MANU') {
          this.iocShow = true
        }
        if (this.ioc !== '' && this.autoText === 'MANU') {
          this.index2 = 3
        }
        if (this.scan !== '' && this.autoText === 'MANU') {
          this.index2 = 3
        }
      } else {
        if (this.gallery >= 0 && this.index2 < 4) {
          this.index2 += 1
        }
        if (this.gallery >= 0) {
          this.clear += 1
        }
      }
    },
    // 
    galleryFun (num) {
      switch (this.index2) {
        case 1:
          this.gallery = this.gallery * 10 + parseInt(num)
          this.gallery = this.gallery % 100
          break
        case 2:
          break
        case 3:
          break
      }
    },
    // 
    clearNum () {
      this.index1 = 0
      this.channel = '' // 
      this.timeText1 = '' // 1
      this.timeText2 = '' // 2
      this.index2 = 0
      this.galleryType = false
      this.gallery = 0 // 
      this.iocShow = false
      this.autoText = '' // 
      this.halfs = ''
      this.aotoNum = 0
      this.ioc = ''
      this.scan = ''
      this.scanShow = false
      this.iocindex = 0
      this.scanindex = 0
      this.progList = false
      this.clear = 0
    },
    // 
    man () {
      if (this.index2 === 2 && this.iocShow === false) {
        if (this.aotoNum === false) {
          this.aotoNum = true
          this.autoText = 'AUTO'
        } else {
          this.aotoNum = false
          this.autoText = 'MANU'
        }
        console.log(this.aotoNum, '444444444444')
      }
    },
    // HALFS
    halfs1 () {
      if (this.autoText === 'AUTO' && this.index2 === 3) {
        this.halfs = 'H'
      }
      if (this.autoText === 'MANU' && this.index2 === 3 && this.scan !== '') {
        this.halfs = 'H'
      }
    },
    autoList () {
      // 
      let channel = this.channel
      // 
      let time1 = this.timeText1[0] + this.timeText1[1] + this.timeText1[2] + this.timeText1[3]
      // 
      let time2 = this.timeText2[0] + this.timeText2[1] + this.timeText2[2] + this.timeText2[3]
      // fch
      let fch = this.gallery
      // 
      let auto = this.autoText
      // HALFS 
      let halfs = this.halfs
      let progObject = {
        chaNum: channel,
        time1Text: time1,
        time2Text: time2,
        fchNum: fch,
        manu: auto,
        half: halfs,
        ioc: this.ioc,
        scan: this.scan
      }
      if (this.progList === false) {
        if (this.halfs !== '' && this.autoText === 'AUTO') {
          this.$frame.gmdss.progAutoList.push(progObject)
          this.clearNum()
          console.log(this.$frame.gmdss.progAutoList)
        }
        if (this.halfs !== '' && this.autoText === 'MANU') {
          this.$frame.gmdss.progManuList.push(progObject)
          this.clearNum()
          console.log(this.$frame.gmdss.progManuList)
        }
      } else {
        // 
        if (this.index2 === 4 && this.clear >= 4) {
          let _this = this
          let dataList = this.$frame.gmdss.progManuList.filter(function (item) {
            return item.chaNum === _this.channel
          })
          dataList[0].ioc = this.ioc
          dataList[0].scan = this.scan
          dataList[0].fchNum = this.gallery
          this.clearNum()
        }
      }
    },
    // 
    autoFn () {
      if (this.index1 === 0) {
        let _this = this
        let dataList = this.$frame.gmdss.progManuList.filter(function (item) {
          return item.chaNum === _this.channel
        })
        // 
        if (dataList[0] === undefined) {
          this.progList = false
          this.timeText1 = ''
          this.timeText2 = ''
          this.iocShow = false
          this.scanShow = false
          this.ioc = ''
          this.scan = ''
          this.halfs = ''
          this.gallery = 0
        } else {
          // 
          this.progList = true
          this.timeText1 = dataList[0].time1Text
          this.timeText2 = dataList[0].time2Text
          this.iocShow = true
          this.scanShow = true
          this.ioc = dataList[0].ioc
          this.scan = dataList[0].scan
          this.halfs = dataList[0].half
          this.gallery = dataList[0].fchNum
        }
      }
    },
    //  
    clearProg () {
      let _this = this
      let dataList = this.$frame.gmdss.progManuList.filter(function (item) {
        return item.chaNum === _this.channel
      })
      // dataList[0] = null
      dataList[0].ioc = ''
      dataList[0].scan = ''
      dataList[0].fchNum = ''
      dataList[0].chaNum = ''
      console.log(this.$frame.gmdss.progManuList)
      this.clearNum()
    }
  },
  [EventKeys.EVT_MAN] (evt) {
    // AUTOMANU
    this.man()
  },
  [EventKeys.EVT_IOC] (evt) {
    // 
    if (this.index2 === 2 && this.iocShow === true) {
      this.iocindex += 1
      this.ioc = this.gmdss.iocList[this.iocindex]
    }
  },
  [EventKeys.EVT_SCAN] (evt) {
    // 
    if (this.ioc !== '' && this.scan === '') {
      this.index2 = 4
      this.scanShow = true
    }
    if (this.scanShow === true && this.index2 === 4) {
      this.scanindex += 1
      this.scan = this.gmdss.scanList[this.scanindex]
    }
  },
  [EventKeys.EVT_PH] (evt) {
  },
  [EventKeys.EVT_DIM] (evt) {
  },
  [EventKeys.EVT_PEED] (evt) {
  },
  [EventKeys.EVT_HALF] (evt) {
    this.halfs1()
  },
  [EventKeys.EVT_PROG] (evt) {
    this.$frame.routeTo('registering')
  },
  [EventKeys.EVT_TIME] (evt) {
  },
  [EventKeys.EVT_MO] (evt) {
  },
  [EventKeys.EVT_CH] (evt) {
  },
  [EventKeys.EVT_ENT] (evt) {
    // 
    if (this.galleryType === false) {
      this.index1Fun()
    } else {
      this.index2Fun()
    }
    // 
    //  this.proglist
    this.autoList()
  },
  [EventKeys.EVT_ONE] (evt) {
    this.channelNum('1')
    this.galleryFun('1')
    this.autoFn()
  },
  [EventKeys.EVT_TWO] (evt) {
    this.channelNum('2')
    this.galleryFun('2')
    this.autoFn()
  },
  [EventKeys.EVT_THREE] (evt) {
    this.channelNum('3')
    this.galleryFun('3')
    this.autoFn()
  },
  [EventKeys.EVT_FOUR] (evt) {
    this.channelNum('4')
    this.galleryFun('4')
    this.autoFn()
  },
  [EventKeys.EVT_FIVE] (evt) {
    this.channelNum('5')
    this.galleryFun('5')
    this.autoFn()
  },
  [EventKeys.EVT_SIX] (evt) {
    this.channelNum('6')
    this.galleryFun('6')
    this.autoFn()
  },
  [EventKeys.EVT_SEVEN] (evt) {
    this.channelNum('7')
    this.galleryFun('7')
    this.autoFn()
  },
  [EventKeys.EVT_EIGHT] (evt) {
    this.channelNum('8')
    this.galleryFun('8')
    this.autoFn()
  },
  [EventKeys.EVT_NINE] (evt) {
    this.channelNum('9')
    this.galleryFun('9')
    this.autoFn()
  },
  [EventKeys.EVT_ZERO] (evt) {
    this.channelNum('0')
    this.galleryFun('0')
    this.autoFn()
  },
  [EventKeys.EVT_CLR] (evt) {
    this.clearProg()
  },
  [EventKeys.EVT_WELL] (evt) {
  }
}
</script>

<style>
.prog{
  width: 100%;
  height: 100%;
  background-color: aquamarine;
  font-weight: bold;
}
.prog-top{
  display: flex;
  padding-top: 3px;
  justify-content: space-around;
}
.prog-top > div {
  width: 10px;
  height: 20px;
  /*border: 1px solid black;*/
  text-align: center;
  line-height: 20px;
}
.prog-bottom{
  margin-top: 3px;
  display: flex;
  justify-content: space-around;
}
.prog-bottom > div{
  width: 10px;
  height: 20px;
  /* border: 1px solid black; */
  line-height: 20px;
  text-align: center;
}
.prog-div{
  width:5px;
  height:7px;
  background-color:black;
  margin-left:2px;
  margin-top:5px;
}
.prog-border{
  border-bottom: 1px solid black;
}
.prog-right{
  width: 20px;
  height: 10px;
  transform: rotate(90deg);
  background-image: url('../assets/arrow.png');
  background-size: 100% 100%;
}
</style>



